.hints-item {
    border: 1px solid #eee;
    padding: 0.5rem;
    margin: 0.5rem 0;
    box-shadow: 3px 3px 20px 10px #eee;
    transition: 200ms;
    position: relative;
}
.hints-item h4{
    margin: 0.5rem 0 !important;
    color: gray;
}
.hints-item span {
    font-size: 14px;
    padding:0!important;
}